    },

    gotoLogin() {
      this.$router.push({ name: "login" });
    }
  },
  created() {
    if (this.$session.get("token")) {
      this.admin = true;
    }

    fetch(process.env.VUE_APP_SERVER_URL + "?mode=1", {
      credentials: "include"
    })
      .then(res => {
        return res.json();
      })
      .then(res => {
        this.yazi = res.reverse();
      });

    Store.$on("EditChanged", () => {
      this.edit = !this.edit;
    });
