  <div class="wrapper" v-if="isLogged">
    <div class="display-flex row p-0 m-0">
      <div class="col-lg-2 w-100 border-right border-danger p-0 m-0 sidebar bg-primary">
        <div class="text-center mt-3 border-black pb-3">
          <img
            class="rounded-circle img-fluid w-50 border border-danger"
            src="../assets/logo.png"
            @click="Detaylandir()"
          />
          <h2 class="mt-3 text-muted admin-paneli">Admin Paneli</h2>
        </div>
        <ul class="list-group">
          <li class="list-group-item">
            <router-link class="text-white" :to="goTo('AnaSayfa')">AnaSayfa</router-link>
          </li>
          <li class="list-group-item">
            <a class="text-white" id="bloglar">Bloglar</a>
            <div id="BloglarDetay" class="d-none">
              <ul class="list-group">
                <li class="list-group-item border-0">
                  <span id="BloglarDetaySpan">
                    <router-link class="text-white" :to="goTo('Bloglar')">> Tm Bloglar</router-link>
                    <router-link class="text-white" :to="goTo('BlogEkle')">> Blog Ekle</router-link>
                  </span>
                </li>
              </ul>
            </div>
          </li>
          <li class="list-group-item">
            <router-link class="text-white" :to="goTo('Yorumlar')">Yorumlar</router-link>
          </li>
        </ul>
      </div>
      <div class="col-lg-10 w-100 p-0 m-0">
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Admin",
  data() {
    return {
      isLogged: false
    };
  },
  created() {
    if (this.$session.get("token")) {
      this.isLogged = true;
    } else {
      this.$router.push({ path: "/login" });
    }
  },
  methods: {
    Detaylandir() {
      this.$router.push({ path: "/" });
    }
  },
  computed: {
    goTo() {
      return rota => `${rota}`;
    }
  },
  mounted() {
    $("#bloglar").click(() => {
      $("#BloglarDetay").toggleClass("d-none");
    });
  }
};
</script>

