<template>
  <div class="py-3">
    <div class="modal" tabindex="-1" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Hata</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p class="m-0" style="font-size: 1.3rem;">{{this.Hata}}</p>
          </div>
        </div>
      </div>
    </div>

    <div class="text-right">
      <button class="btn btn-info text-right px-4 pt-2" v-if="edit" @click="Duzenle">Kaydet</button>
    </div>
    <timeago class="timeago" :datetime="yazi.Tarih" locale="tr"></timeago>
    <h2 class="font-weight-bold text-success mr-3 mb-3" v-if="!edit" v-html="yazi.Baslik"></h2>
    <editor
      apiKey="5vodwm3bmkeuphl33gdk756mvcqba5lxqarzyr5tcrw48jl5"
      v-if="edit"
      class="font-weight-bold text-success mr-3 mb-3 baslik"
      v-model="yazi.Baslik"
      :inline="true"
      :init="{ entity_encoding : 'raw' }"
    />
    <div class="icerik text-dark" v-html="yazi.Icerik" v-if="!edit"></div>
    <editor
      apiKey="5vodwm3bmkeuphl33gdk756mvcqba5lxqarzyr5tcrw48jl5"
      v-if="edit"
      class="icerik text-dark"
      v-model="yazi.Icerik"
      :inline="true"
      :init="{ entity_encoding : 'raw' }"
    />
    <hr />

    <h4 class="pb-2">
      <b>Yorum Yaz</b>
    </h4>

    <form onsubmit="return false;">
      <div class="row">
        <div class="form-group col-lg-6">
          <label>sim</label>
          <input type="text" class="form-control" placeholder="sim" v-model="Isim" />
        </div>
        <div class="form-group col-lg-6">
          <label>Email</label>
          <input type="email" class="form-control" placeholder="Email" v-model="Email" />
        </div>
        <div class="form-group col-lg-12">
          <label>Yorum</label>
          <textarea
            rows="5"
            class="form-control"
            placeholder="Yorum (En az 10 karakter)"
            v-model="Yorum"
          />
        </div>
      </div>
      <button class="btn btn-danger px-5 py-2" id="btnSubmit">Gnder</button>
    </form>

    <hr />

    <div class="col-lg-12">
      <h4>
        <b>Tm Yorumlar</b>
      </h4>
      <div v-for="item in yazi.Yorumlar" :key="item._id">
        <yorum v-bind="item" />
      </div>
    </div>
  </div>
</template>

<script>
import Store from "../store/index.js";
import Yorum from "../components/Yorum.vue";
import Editor from "@tinymce/tinymce-vue";

export default {
  name: "BlogYazisiDetay",
  components: {
    Yorum,
    editor: Editor
  },
  data: () => {
    return {
      yazi: {},
      edit: false,
      Isim: "",
      Email: "",
      Yorum: "",
      Hata: ""
    };
  },
  watch: {
    $route: "fetchData"
  },
  created() {
    this.edit = Store.edit;

    this.fetchData();

    Store.$on("EditChanged", () => {
      this.edit = Store.edit;
    });
  },
  methods: {
    fetchData() {
      fetch(process.env.VUE_APP_SERVER_URL + this.$route.params.id + "?mode=2")
        .then(res => {
          return res.json();
        })
        .then(res => {
          this.yazi = res;
        });
    },

    updatEdit() {
      Store.$emit("EditChanged");
    },

    Duzenle() {
      fetch(`${process.env.VUE_APP_SERVER_URL}${this.yazi.Sira}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
          token: this.$session.get("token")
        },
        body: JSON.stringify({
          Baslik: this.yazi.Baslik,
          Icerik: this.yazi.Icerik
        })
      })
        .then(res => {
          if (res.status == 200) location.reload();
          else return res.json();
        })
        .then(res => {
          console.log(res);
          let result = Object.entries(res.errors);

          for (let i = 0; i < result.length; i++) {
            for (let z = 0; z < result[i].length; z++) {
              result[i][z].message
                ? (this.Hata += result[i][z].message + "\n")
                : null;
            }
          }
          $(".modal").modal("show");
        });
    }
  },
  mounted() {
    $("#btnSubmit").on("click", () => {
      var settings = {
        url: `${process.env.VUE_APP_SERVER_URL}${this.yazi.Sira}`,
        method: "POST",
        timeout: 0,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        data: {
          Isim: this.Isim,
          Email: this.Email,
          Yorum: this.Yorum,
          Tarih: Date.now()
        }
      };

      $.ajax(settings).done(response => {
        // location.reload();
      });
    });
  }
};
</script>

<style scoped>
.icerik {
  width: 100%;
  border: 0px;
}

.baslik {
  font-size: 2rem;
}

.timeago {
  font-size: 14px;
  color: #6c757d !important;
  padding-left: 0.5rem !important;
  margin: 0 !important;
  font-style: italic !important;
}
</style>