<template>
  <div class="row pt-5 mt-5">
    <div class="col-lg-4 mx-auto p-5 border border-danger">
      <router-link to="/">
        <h2 class="mb-5">Ana Sayfaya Dn</h2>
      </router-link>
      <form onsubmit="return false;">
        <div class="form-group">
          <label for="emailInput">Email adresi</label>
          <input type="email" class="form-control" id="emailInput" v-model="Email" />
        </div>
        <div class="form-group">
          <label for="passInput">Parola</label>
          <input type="password" class="form-control" id="passInput" v-model="Parola" />
        </div>
        <div class="form-group form-check">
          <input type="checkbox" class="form-check-input" id="hatirlaInput" />
          <label class="form-check-label" for="hatirlaInput">Beni Hatrla</label>
        </div>
        <button type="submit" class="btn btn-primary" @click="LoginOl">Giri Yap</button>
      </form>
    </div>
  </div>
</template>

<script>
const axios = require("axios");

export default {
  name: "Login",
  data() {
    return {
      Email: "",
      Parola: ""
    };
  },
  methods: {
    LoginOl() {
      axios
        .post(process.env.VUE_APP_SERVER_URL + "login", {
          Email: this.Email,
          Parola: this.Parola
        })
        .then(response => {
          if (response.status == 200) {
            this.$session.start();
            this.$session.set("token", response.data);
            this.$router.push({ path: "/" });
          }
        })
        .catch(function(error) {
          console.log("error handled", error);
          alert("Email adresi veya ifre yanl");
        });
    }
  }
};
</script>

<style scoped>
div.col-lg-4 {
  background-color: rgb(230, 208, 208);
}
</style>
