<template>
  <div>
    <div v-for="item in yazi" :key="item._id">
      <blog-yazisi v-bind="item" :id="item.Sira" />
    </div>

    <div class="text-center">
      <span v-for="item in Math.ceil(dataCount / 10)" :key="item">
        <router-link :to="gotoPage(item)">{{item}}</router-link>
      </span>
    </div>
  </div>
</template>

<script>
import BlogYazisi from "../components/BlogYazisi.vue";

export default {
  name: "Anasayfa",
  components: {
    BlogYazisi
  },
  data: () => {
    return { yazi: [], dataCount: 0 };
  },
  computed: {
    gotoPage() {
      return item => `/?p=${item}`;
    }
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      fetch(
        process.env.VUE_APP_SERVER_URL +
          "?mode=1&p=" +
          (this.$route.query.p ? this.$route.query.p : 1)
      )
        .then(res => {
          return res.json();
        })
        .then(res => {
          this.yazi = null;
          this.yazi = res;
          window.scrollTo(0, 0);
        });
    },

    sortArrays(arrays) {
      return this.yazi.sort("Tarih");
    }
  },
  created() {
    const redirectUrl = this.$route.query.redirect;
    if (redirectUrl) {
      return this.$router.push({ path: `/${redirectUrl}` });
    }

    this.fetchData();

    fetch(process.env.VUE_APP_SERVER_URL + "?mode=3")
      .then(res => {
        return res.json();
      })
      .then(res => {
        this.dataCount = res;
      });

    if (this.$session.get("token")) {
      this.admin = true;
    }
  }
};
</script>

