import Vue from 'vue'
import VueRouter from 'vue-router'

import Anasayfa from '../views/Anasayfa.vue'
import Hakkimda from '../views/Hakkimda.vue'
import Iletisim from '../views/Iletisim.vue'
import BlogYazisiDetay from '../views/BlogYazisiDetay.vue'
import Login from '../views/Login.vue'
import AdminArea from './admin.js'


Vue.use(VueRouter)

const routes = [{
    path: '*',
    name: 'a404',
    meta: { layout: "a404" }
},
{
    path: '/',
    name: 'Anasayfa',
    component: Anasayfa
},
{
    path: '/hakkimda',
    name: 'Hakkimda',
    component: Hakkimda
},
{
    path: '/iletisim',
    name: 'Iletisim',
    component: Iletisim
},
{
    path: '/Yazlar/:id',
    name: 'BlogYazisiDetay',
    component: BlogYazisiDetay
},
{
    path: '/login',
    name: 'login',
    meta: { layout: "empty" },
    component: Login
},
...AdminArea,
]

const router = new VueRouter({
    base: process.env.BASE_URL,
    routes,
    mode: 'history'
})

export default router