<template>
  <div>
    <timeago class="timeago" :datetime="Tarih" locale="tr"></timeago>
    <router-link :to="goToBlogYazisi(id)">
      <h4 class="baslik font-weight-bold text-success" v-html="Baslik"></h4>
    </router-link>
    <div>
      <p class="ozet text-dark" v-if="!edit" v-html="DataOzet"></p>
    </div>
    <editor
      class="ozet p-0 m-0"
      apiKey="5vodwm3bmkeuphl33gdk756mvcqba5lxqarzyr5tcrw48jl5"
      v-if="edit"
      v-model="DataOzet"
      :inline="true"
      @onBlur="Duzenle"
      :init="{
        force_p_newlines: true,
        entity_encoding : 'raw'
      }"
    />
    <hr class="border-dark mx-1" />
  </div>
</template>>

<script>
import Store from "../store/index.js";
import Editor from "@tinymce/tinymce-vue";

export default {
  name: "BlogYazisi",
  components: {
    editor: Editor
  },
  data: () => {
    return { edit: false, DataOzet: "" };
  },
  props: {
    id: Number,
    Baslik: String,
    Tarih: String,
    Ozet: String
  },
  created() {
    this.edit = Store.edit;
    this.DataOzet = this.$props.Ozet;

    Store.$on("EditChanged", () => {
      this.edit = Store.edit;
    });
  },
  computed: {
    goToBlogYazisi() {
      return id => `Yazlar/${id}`;
    }
  },
  methods: {
    updatEdit() {
      Store.$emit("EditChanged");
    },

    Duzenle() {
      var settings = {
        url: `${process.env.VUE_APP_SERVER_URL}${this.$props.id}`,
        method: "PUT",
        timeout: 0,
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
          token: this.$session.get("token")
        },
        data: {
          Ozet: this.DataOzet.replace("<p>", "").replace("</p>", "")
        }
      };

      $.ajax(settings).done(response => {
        console.log(response);
      });
    }
  }
};
</script>

